div.stars {
  width: 270px;
  display: inline-block;
}

 .mt-200{
     margin-top:200px;  
 }

input.star { display: none; }

 
label.star {
  float: right;
  padding: 10px;
  font-size: 36px;
  color: #4A148C;
  transition: all .2s;
}

input.star:checked ~ label.star:before {
  font-family: "Font Awesome 5 Free";
  text-rendering: optimizeLegibility;
  content: '\f005';
  color: #FD4;
  transition: all .25s;
}

 
input.star-5:checked ~ label.star:before {
  color: #FE7;
  text-shadow: 0 0 20px #952;
}

input.star-1:checked ~ label.star:before { color: #F62; } 
label.star:hover { -webkit-transform: rotate(-15deg) scale(1.3); transform: rotate(-15deg) scale(1.3); }

label.star:before {
  text-rendering: optimizeLegibility;
  font-family: 'Font Awesome 5 Free';
  content: '';
}

